
import * as _ from "ramda";
import Group from 'vux/src/components/group/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import api from "@/api/api";

let _nationality = ``.split(
""
);

function _loop(list, min, max, p) {
while (min < max) {
list.push(p(min));
min++;
}
return list;
}

let _height = _loop(["150"], 151, 200, p => `${p}cm`);
_height.push("200");

let _weight = _loop(["40kg"], 41, 99, p => `${p}kg`);
_weight.push("100");

let _hobbies = ``.split(
""
);
let _label = `ITTVBK`.split(
""
);

let _forAge = _loop([], 18, 50, p => `${p}`);

export default {
components: {
Group,
GroupTitle,
XInput,
Selector,
Flexbox,
FlexboxItem,
XButton,
Datetime,
ChinaAddressData,
XAddress,
XTextarea,
Scroller,
Checker,
CheckerItem
},
watch: {
$route(to, from) {
this.id32 = to.query.id32;
this.loadUserDetail();
}
},
data() {
return {
pagePrivate: {
nationality: _nationality,
height: _height,
weight: _weight,
education: ["", "", "", "", "", ""],
education1: ["", "", "", "", "", ""],
constellation: ``.split(
""
),
salary: [
"5",
"5-10",
"10-15",
"15-20",
"20-30",
"30-50",
"50-100",
"100"
],
salary1: [
"",
"10",
"15",
"20",
"30",
"50"
],
addressData: ChinaAddressData,
hobbies: _hobbies,
label: _label,
forAge: _forAge
},

id32: "",
entity: {
attachHobbies: "",
attachLabel: "",
id: 1,
userid: 1,
wx: "",
phone: "",
name: "",
gender: "",
nationality: "",
birth: "0001-01-01",
height: "",
weight: "0",
education: "",
constellation: "",
profession: "",
salary: "",
car: "",
marriage: "",
house: "",
address: ["", "", ""],
hometown: ["", "", ""],
sex: "",
smoke: "",
drink: "",
pet: "",
diet: "",
hobbies: "",
label: "",
selfIntroduction: "",
forAge1: "",
forAge2: "",
forHeight1: "",
forHeight2: "",
forWeight1: "",
forWeight2: "",
forEducation: "",
forSalary: "",
forCar: "",
forHouse: "",
forAddress: "",
forHometown: "",
forSex: "",
forSmoke: "",
forDrink: "",
forPet: "",
forOther: ""
}
};
},
created() {
console.log(this.$route);
this.id32 = this.$route.query.id32;
this.loadUserDetail();
},
methods: {
async loadUserDetail() {
this.$vux.loading.show({ text: "..." });
console.log("id32:" + this.id32);
let res = await api.user.getDetail(this.id32);
if (res.success) {
var entity = res.data;
if (_.isEmpty(entity.phone)) {
entity.phone = this.$ls.get("phone");
}
if (_.isEmpty(entity.gender)) entity.gender = "";
if (_.isEmpty(entity.nationality)) entity.nationality = "";
if (_.isEmpty(entity.birth)) entity.birth = "1990-01-01";
if (_.isEmpty(entity.height)) entity.height = "170cm";
if (_.isEmpty(entity.weight)) entity.weight = "50kg";
if (_.isEmpty(entity.education)) entity.education = "";
if (_.isEmpty(entity.marriage)) entity.marriage = "";

if (_.isEmpty(entity.address)) {
console.log(11);
entity.address = ["", "", ""];
} else {
console.log(22);
entity.address = entity.address.split(",");
}
if (_.isEmpty(entity.hometown)) {
console.log(11);
entity.hometown = ["", "", ""];
} else {
console.log(22);
entity.hometown = entity.hometown.split(",");
}
entity.attachHobbies = "";
entity.attachLabel = "";
entity.hobbies = entity.hobbies.split(" ") || [];
entity.label = entity.label.split(" ") || [];

this.pagePrivate.hobbies = _.uniq(
this.pagePrivate.hobbies.concat(entity.hobbies)
);
this.pagePrivate.label = _.uniq(
this.pagePrivate.label.concat(entity.label)
);
if (_.isEmpty(entity.forAge)) {
entity.forAge1 = "";
entity.forAge2 = "";
} else {
entity.forAge1 = entity.forAge.split("-")[0];
entity.forAge2 = entity.forAge.split("-")[1];
}
delete entity.forAge;

if (_.isEmpty(entity.forHeight)) {
entity.forHeight1 = "";
entity.forHeight2 = "";
} else {
entity.forHeight1 = entity.forHeight.split("-")[0];
entity.forHeight2 = entity.forHeight.split("-")[1];
}
delete entity.forHeight;

if (_.isEmpty(entity.forWeight)) {
entity.forWeight1 = "";
entity.forWeight2 = "";
} else {
entity.forWeight1 = entity.forWeight.split("-")[0];
entity.forWeight2 = entity.forWeight.split("-")[1];
}
delete entity.forWeight;

this.entity = entity;
} else {
alert(res.msg);
}
this.$vux.loading.hide();
},
async save() {
let entity = _.clone(this.entity);
entity.userid = this.$ls.get('userid')
entity.address = entity.address.join(",");
entity.hometown = entity.hometown.join(",");
entity.forAge = `${entity.forAge1}-${entity.forAge2}`;
entity.forHeight = `${entity.forWeight1}-${entity.c}`;
entity.forWeight = `${entity.forWeight1}-${entity.forWeight2}`;
entity.hobbies = entity.hobbies
.concat(entity.attachHobbies.split(" "))
.join(" ");
entity.label = entity.label
.concat(entity.attachLabel.split(" "))
.join(" ");
delete entity.attachHobbies;
delete entity.attachLabel;
delete entity.forAge1;
delete entity.forAge2;
delete entity.forWeight1;
delete entity.attachLabel;
delete entity.forWeight2;
delete entity.forWeight2;

this.$vux.loading.show({ text: "..." });
let res = await api.user.saveDetail(entity, this.id32);
this.$vux.loading.hide();
if (res.success) {
this.$vux.alert.show({
title: "",
content: ""
});
} else {
this.$vux.alert.show({
title: "",
content: res.msg
});
}
}
}
};



